  width: 1em;
  height: 1em; }

[hidden] {
  display: none; }

@media (min-width: 47.5em) {
  .hide_desktop {
    display: none; } }

@media (max-width: 47.5em) {
  .hide_mobile {
    display: none; } }

.menu-icon {
  display: inline-block;
  position: relative; }

.menu-icon_path {
  background: currentColor;
  position: absolute;
  width: 56.56854248%;
  transform-origin: left center;
  top: 85%;
  left: 10%; }

.menu-icon_path.path--a {
  top: 5%; }

.menu-icon_path.path--b {
  width: 80%; }

.menu-icon_path.path--c {
  transform-origin: right center; }

body > header {
  font-size: 1.25rem;
  line-height: 1.6;
  padding-bottom: 0.51875rem;
  padding-top: 1.48125rem;
  text-align: right; }
  body > header .menu-icon {
    width: 1rem;
    height: 1rem;
    transition: 0.3s all 0s; }
    body > header .menu-icon_path {
      height: 0.1rem;
      border-radius: 0.05rem;
      transition: 0.3s transform cubic-bezier(0, 0, 0.4, 1.2) 0s, 0.3s height 0s; }
      body > header .menu-icon_path.path--b {
        transform: translateY(-0.4rem); }
  .header_menu-button {
    display: none; }
  body > header li {
    display: inline-block;
    margin-right: 2.7027027027%; }
  body > header a {
    color: inherit;
    text-decoration: none; }
  body > header svg {
    height: 0.9090909091em;
    width: 1em; }

@media (max-width: 760px) {
  body > header {
    color: #FAFAFA;
    font-size: 1.25rem;
    height: 3rem;
    padding: 0; }
    input:checked ~ header .menu-icon {
      width: 2rem;
      height: 2rem;
      transition: 0.3s all 0.1s; }
      input:checked ~ header .menu-icon_path {
        height: 0.2rem;
        border-radius: 0.1rem;
        transition: 0.3s transform cubic-bezier(0, 0, 0.4, 1.2) 0.1s, 0.3s height 0.1s; }
        input:checked ~ header .menu-icon_path.path--a {
          transform: rotate(45deg); }
        input:checked ~ header .menu-icon_path.path--b {
          transform: rotate(-45deg) scaleX(1.414213562); }
        input:checked ~ header .menu-icon_path.path--c {
          transform: translateX(0.4686291504rem) rotate(45deg); }
    body > header::before {
      background: rgba(218, 27, 27, 0.7);
      content: "";
      display: block;
      height: 1rem;
      position: fixed;
      right: 6.25%;
      top: 1rem;
      width: 0;
      z-index: 1;
      transition: 0.4s cubic-bezier(0.6, 0, 0.7, 0.8) height, 0.4s cubic-bezier(0.6, 0, 0.7, 0.8) right, 0.4s cubic-bezier(0.6, 0, 0.7, 0.8) top, 0.4s width; }
      input:checked ~ header::before {
        height: 100%;
        right: 0;
        top: 0;
        width: 100%;
        transition: 0.4s height, 0.4s right, 0.4s top, 0.4s width cubic-bezier(0.6, 0, 0.7, 0.8); }
    body > header nav {
      visibility: hidden;
      position: fixed;
      width: 87.5%;
      height: 100%;
      left: 6.25%;
      top: 0;
      transform: translateX(100%);
      z-index: 3;
      transition: .4s transform, .3s opacity, visibility 0s .4s; }
      input:checked ~ header nav {
        visibility: visible;
        transform: translateX(0%);
        opacity: 1;
        transition: 0.4s transform cubic-bezier(0.6, 0, 0.7, 0.8), 0.3s opacity 0.1s; }
    body > header ul {
      display: flex;
      height: 100%;
      flex-direction: column; }
    body > header li {
      display: flex;
      flex-grow: 1;
      align-items: center;
      text-align: center;
      margin-right: 0; }
    body > header a {
      width: 100%; }
  .header_menu-button {
    display: block;
    font-size: 0.875rem;
    position: absolute;
    right: 6.25%;
    top: 0.636875rem;
    line-height: 2.2857142857;
    color: #545454;
    cursor: pointer;
    transition: .2s color .2s, .3s box-shadow, margin-top .2s; }
  .header_menu-icon {
    margin-left: 1rem;
    color: #545454;
    z-index: 4; }
  input:checked ~ header .header_menu-button {
    font-size: 0;
    color: rgba(84, 84, 84, 0);
    transition: .2s color, 0s font-size .4s, margin-top .2s;
    margin-top: 1.363125rem; }
  input:checked ~ header .header_menu-icon {
    color: #FAFAFA;
    margin-left: 0; }
  input:checked ~ header .header_menu::before {
    content: "";
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2; } }

